.search-form {
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 20px;
  padding: 20px;
  display: flex;
}

.search-form input {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 300px;
  margin-right: 10px;
  padding: 10px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  transition: background-color .3s;
}

.search-form button:hover {
  background-color: #0056b3;
}

.section {
  background-color: #a9a9a9;
  border-radius: 15px;
  margin: 0 auto;
  padding: 40px 20px;
  transition: transform .3s;
  box-shadow: 0 10px 20px #0000001a;
}

.section:hover {
  transform: translateY(5px);
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  padding: 20px;
  display: grid;
}

.photo-card {
  background: #fff;
  border-radius: 10px;
  transition: transform .3s, box-shadow .3s;
  overflow: hidden;
  box-shadow: 0 4px 8px #0003;
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

.photo-card .info {
  text-align: center;
  padding: 15px;
  font-family: Arial, sans-serif;
}

.photo-card .info-item {
  color: #333;
  margin: 5px 0;
  font-size: 14px;
}

.photo-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 16px #0000004d;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s, transform .3s;
